body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
td, th {
  vertical-align: middle !important;
  text-align: center !important;
}

.input-group {
  margin: 20px 0;
}

svg {
  height: 120px;
  width: 250px;
}

td:first-of-type, td:first-of-type > div {
  height: 200px;
  width: 250px;
}



